@media(max-width: 767px) {
    :root {
		--fz: 14px !important;
	}
	.section-1{
		&__descr{
			font-size: var(--fz-l);
		}
	}
    .aside-btn{
		height: 120px;
	}
	.header {
		&__num{
			margin-right: 10px;
		}
	}
	.title::after,
	.section-1 .intro-slider__descr::after {
		top: -15px !important;
	}
	.section-4{
		&-title {
			margin-bottom: 10px;
		}
	}
}
